import React, { useRef, useEffect, useState } from "react";
import { motion } from "framer-motion";

const clothes = [
  { name: "Red Tee", img: "/images/red1.jpeg" },
  { name: "Green Tee", img: "/images/green1.jpeg" },
  { name: "White Tee", img: "/images/white1.jpeg" },
  { name: "Black Tee", img: "/images/black1.jpeg" },
  { name: "Red Tee 2", img: "/images/red2.jpeg" },
  { name: "Green Tee 2", img: "/images/green2.jpeg" },
  { name: "White Tee 2", img: "/images/white 2.jpeg" },
  { name: "Black Tee 2", img: "/images/black2.jpeg" },
  { name: "Tshirt 1", img: "/images/tshirt1.jpeg" },
  { name: "Tshirt Plain", img: "/images/tshirt plain.jpeg" },
  { name: "Tshirt Back", img: "/images/tshirt back.jpeg" },
];

export default function ClothesScroller() {
  const scrollerRef = useRef<HTMLDivElement>(null);
  const [isDragging, setIsDragging] = useState(false);
  const [scrollSpeed, setScrollSpeed] = useState(0.5); // px per frame
  const [dragStartX, setDragStartX] = useState<number | null>(null);
  const [scrollLeftStart, setScrollLeftStart] = useState<number>(0);
  const [velocity, setVelocity] = useState(0);

  // Auto-scroll effect
  useEffect(() => {
    let frame: number;
    function animate() {
      if (scrollerRef.current && !isDragging) {
        scrollerRef.current.scrollLeft += scrollSpeed;
      }
      frame = requestAnimationFrame(animate);
    }
    animate();
    return () => cancelAnimationFrame(frame);
  }, [scrollSpeed, isDragging]);

  // Speed up auto-scroll when user scrolls the page
  useEffect(() => {
    function handleScroll() {
      setScrollSpeed(3); // Fast scroll when user scrolls
      setTimeout(() => setScrollSpeed(0.5), 800); // Return to slow after 0.8s
    }
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  // Drag to scroll (pointer events for better support)
  function handlePointerDown(e: React.PointerEvent) {
    setIsDragging(true);
    setDragStartX(e.clientX);
    setScrollLeftStart(scrollerRef.current?.scrollLeft || 0);
    setVelocity(0);
    (e.target as HTMLElement).setPointerCapture(e.pointerId);
  }
  function handlePointerMove(e: React.PointerEvent) {
    if (isDragging && dragStartX !== null && scrollerRef.current) {
      const dx = dragStartX - e.clientX;
      // Increase sensitivity (multiply by 1.5)
      scrollerRef.current.scrollLeft = scrollLeftStart + dx * 1.5;
      // Track velocity for momentum
      setVelocity(e.movementX * 2); // More sensitive
      // Prevent vertical scroll while dragging horizontally
      e.preventDefault();
    }
  }
  function handlePointerUp(e: React.PointerEvent) {
    setIsDragging(false);
    setDragStartX(null);
    (e.target as HTMLElement).releasePointerCapture(e.pointerId);
  }

  // Add momentum after drag ends
  useEffect(() => {
    if (!isDragging && Math.abs(velocity) > 0.5 && scrollerRef.current) {
      let frame: number;
      function momentum() {
        if (scrollerRef.current) {
          scrollerRef.current.scrollLeft -= velocity;
          setVelocity(v => v * 0.92); // friction
          if (Math.abs(velocity) > 0.5) {
            frame = requestAnimationFrame(momentum);
          }
        }
      }
      momentum();
      return () => cancelAnimationFrame(frame);
    }
  }, [isDragging, velocity]);

  return (
    <div style={{ width: "100vw", overflow: "hidden", padding: "48px 0", background: "#fafafa" }}>
      <h2 style={{ textAlign: "center", fontSize: "2rem", marginBottom: 32 }}>New Collection</h2>
      <div
        ref={scrollerRef}
        style={{
          display: "flex",
          gap: "48px",
          overflowX: "auto",
          cursor: isDragging ? "grabbing" : "grab",
          padding: "16px 0",
          scrollBehavior: "smooth",
          userSelect: "none",
        }}
        onPointerDown={handlePointerDown}
        onPointerMove={handlePointerMove}
        onPointerUp={handlePointerUp}
        onPointerLeave={handlePointerUp}
      >
        {clothes.map((item, idx) => (
          <motion.div
            key={item.name + idx}
            style={{
              minWidth: 320,
              height: 420,
              background: "#fff",
              borderRadius: 32,
              boxShadow: "0 4px 24px #ddd",
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              justifyContent: "center",
              padding: 24,
              position: "relative",
            }}
            whileHover={{ scale: 1.06 }}
          >
            <img
              src={item.img}
              alt={item.name}
              style={{ width: 260, height: 320, objectFit: "cover", borderRadius: 24, marginBottom: 18 }}
            />
            <div style={{ fontWeight: "bold", fontSize: "1.2rem", textAlign: "center" }}>{item.name}</div>
          </motion.div>
        ))}
      </div>
    </div>
  );
}
